import React, { useEffect, useState } from 'react';

import UsersList from '../components/UsersList';
import ErrorModal from '../../shared/components/UIElements/ErrorModal';
import LoadingSpinner from '../../shared/components/UIElements/LoadingSpinner';
import { useHttpClient } from '../../shared/hooks/http-hook';

const Users = () => {
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  const [loadedUsers, setLoadedUsers] = useState();

  // combined componentDidMount, componentDidUpdate, and componentWillUnmount;
  // tell React that your component needs to do something after render. 
  // React will remember the function you passed (we’ll refer to it as our “effect”), 
  // and call it later after performing the DOM updates;
  // [] => apendency: Here in useEffect you can pass an array as the second argument. 
  //                  This array clearly tells react that just call useEffect when fields in me has been changed. 
  //                  So if you put ”name” in the array and pass it as second argument of useEffect(); 
  //                  your method will be called just when “name” changes.
  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const responseData = await sendRequest(process.env.REACT_APP_BACKEND_URL + '/users');

        setLoadedUsers(responseData.users);
      } catch (err) {}
    };
    fetchUsers();
  }, [sendRequest]);

  return (
    <React.Fragment>
      <ErrorModal error={error} onClear={clearError} />
      {isLoading && (
        <div className="center">
          <LoadingSpinner />
        </div>
      )}
      {!isLoading && loadedUsers && <UsersList items={loadedUsers} />}
    </React.Fragment>
  );
};

export default Users;
